@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap);

.weather {
    margin-top: 20px;
    max-width: 250px;
}

.weather__items {
    padding: 10px;
    margin: 0;
    display: flex;
    flex-direction: column;
    list-style: none;
}

.weather__item {
    display: flex;
    justify-content: space-between;
}

.weather__icon {
    object-fit: cover;
}

.weather__description {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    margin-top: 10px;
}

.weather__description:before {
    display: inline-block;
    margin-left: 10px;
}
.weather__wind {
    display: flex;
}
.weather__description-wind-arrow::before {
    width: 10px;
    content: url(/react-weather-api/static/media/navigation-cursor.d54c7a4e.svg);
}

.weather__image {
    justify-content: center;   
}

.weather__title {
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

.weather__temperature {
    margin-left: 10px;
    font-family: 'Roboto', sans-serif;
    font-size: 40px;
    font-weight: bold;
    justify-self: left;
}

.weather__setting {
    width: 20px;
    cursor: pointer;
}
@-webkit-keyframes shake {
    0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
    10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
    20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
    30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
    40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
    50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
    60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
    70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
    80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
    90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
    100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

.setting__input-error {
    -webkit-animation: shake 0.5s;
            animation: shake 0.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }

.setting {
    margin-top: 20px;
    position: relative;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 230px;
}

.setting__title {
    font-family: 'Roboto', sans-serif;
    margin: 0;
}

.setting__button-close {
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
    border: 0;
    width: 20px;
    height: 20px;
    background-image: url(data:image/png;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgb(255, 255, 255);
    transition: opacity .5s;
}

.setting__button-close:hover {
    opacity: .2;
}

.setting__button-city-enter {
    width: 25px;
    height: 25px;
    border: 0;
    background-image: url(data:image/png;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgb(255, 255, 255);
}

.setting__button-remove {
    position: absolute;
    right: 10px;
    background-color: transparent;
    background-image: url(/react-weather-api/static/media/baskets.061cceed.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    cursor: pointer;
    width: 20px;
    height: 20px;
    border: 0;
    transition: opacity .5s;
}

.setting__button-remove:hover {
    opacity: .2;
}

.setting__form {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.setting__input {
    margin-top: 10px;
    font-family: 'Roboto', sans-serif;
    font-size: inherit;
    padding-left: 10px;
    padding-right: 10px;
    height: 34px;
    border: 2px solid cornflowerblue;
    border-radius: 3px;
    width: 80%;
}

.setting__form-container {
    margin-top: 50px;
}

.setting__list {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    margin-top: 20px;
    list-style: none;
}

.setting__list-item {
    position: relative;
    padding-left: 10px;
    padding-right: 10px;
    font-family: 'Roboto', sans-serif;
    display: flex;
    align-items: center;
    background-color:#ececec;
    margin-top: 10px;
}

.setting__list-city {
    margin-left: 5px;
}

.setting__list-burger {
    cursor: move;
    width: 25px;
    height: 25px;
}
.loading {
    height: 60px;
}
